import scrollindi from "../components/scrollindi.vue";
import gsap from "gsap";
import ScrollTrigger from "gsap/dist/ScrollTrigger";
import footercontact from "../components/footercontact.vue";

gsap.registerPlugin(ScrollTrigger);

export default {
  components: {
    scrollindi,
    footercontact,
  },
  data() {
    return {
      title: "About Us | Concord Media",
    };
  },
  head() {
    return {
      title: this.title,
    };
  },
  mounted: function () {
    this.scrollAnimation();
  },

  layout: "default",

  methods: {
    scrollAnimation() {
      gsap
        .timeline({
          scrollTrigger: {
            trigger: "#section1",
            start: "center center",
            end: "bottom top",
            // markers: true,
            scrub: true,
            // pin: true,
          },
        })
        .to(".about-pc", { yPercent: -70, duration: 1.5 });
      gsap
        .timeline({
          scrollTrigger: {
            trigger: "#section5",
            start: "center center",
            end: "bottom center",
            // markers: true,
            scrub: true,
            // pin: true,
          },
        })
        .to(".about-pc", { opacity: 0, duration: 0.5 });
    },
  },
};
